              "Az AIRAC, AIC, NOTAM s AIP a lgikzlekedsben hasznlt fontos dokumentumok rvidtsei:&lt;br&gt;&lt;br&gt;&lt;br&gt;&lt;br&gt;AIRAC: Az 'Aeronautical Information Regulation And Control' rvidtse, amely egy olyan rendszer, amely az ves lgikzlekedsi temtervet s az ahhoz kapcsold informcikat hatrozza meg. Az AIRAC rendszer lnyege, hogy az informcik meghatrozott idszakokban (ltalban 28 naponta) frisslnek, gy a piltk s a lgiforgalmi irnytk mindig az aktulis informcikra tmaszkodhatnak a repls tervezse sorn.&lt;br&gt;&lt;br&gt;AIC: Az 'Aeronautical Information Circular' rvidtse, amely egy olyan dokumentum, amelyben az illetkes hatsgok fontos informcikat s ajnlsokat kzlnek a lgikzlekedssel kapcsolatosan. Az AIC-ek szles kr tmkat fedhetnek le, pldul a lgi navigcis segdletek karbantartst, a repltri biztonsgi eljrsokat, a replgp-karbantartst s egyb szablyozsokat.&lt;br&gt;&lt;br&gt;AIP: Az 'Aeronautical Information Publication' rvidtse, amely egy hivatalos dokumentum, amely tartalmazza az orszg lgiforgalmi szolglatnak sszes lgi trkpt, lgi navigcis segdleteit, lgikzlekedsi szablyozsait, lgikzlekedsi eljrsait, replterek adatait s ms fontos informcikat. Az AIP egy adott orszg vagy rgi hivatalos lgikzlekedsi dokumentuma, amelyet a piltk s a lgiforgalmi irnytk hasznlnak a replsi tevkenysgek tervezse s vgrehajtsa sorn. Az AIP frisstse a AIRAC rendszer rsze.&lt;br&gt;&lt;br&gt;A NOTAM-ok (Notice to Airmen) klnbz tpus rtestseket tartalmazhatnak, amelyek kztt a legfontosabbak a kvetkezk:&lt;br&gt;&lt;br&gt;&lt;br&gt;&lt;br&gt;FDC NOTAM: Az Amerikai Szvetsgi Lggyi Hatsg (FAA) ltal kiadott rtestsek, amelyek az orszg egsz terletn rvnyesek. Ezek ltalban olyan informcikat tartalmaznak, mint a lgi tr zrlatai, a katonai gyakorlatok s az tmeneti lgi navigcis segdletek mkdsi zavarai.&lt;br&gt;&lt;br&gt;NOTAM-D: Az USA-beli katonai lgi erk ltal kiadott rtestsek, amelyek a replterek s a katonai lgtr mkdsvel kapcsolatos informcikat tartalmaznak.&lt;br&gt;&lt;br&gt;NOTAMN: Nemzeti fontossg rtestsek, amelyek a nemzetbiztonsgra s a replsbiztonsgra vonatkoz fontos informcikat tartalmazzk.&lt;br&gt;&lt;br&gt;NOTAMR: Az tmeneti vltozsokkal, zrlatokkal vagy korltozsokkal kapcsolatos rtestsek, amelyek csak egy bizonyos idszakban rvnyesek.&lt;br&gt;&lt;br&gt;NOTAMC: A hossz tv vagy lland vltozsokkal kapcsolatos rtestsek, amelyek hosszabb ideig rvnyesek s ltalban az lland replterek s lgi navigcis segdletek mkdsvel kapcsolatos informcikat tartalmazzk.&lt;br&gt;&lt;br&gt;SNOWTAM: A heltakartssal s skossgmentestssel kapcsolatos rtestsek, amelyek a tli idszakban fontosak lehetnek a replgpek biztonsgos mkdse szempontjbl.&lt;br&gt;&lt;br&gt;ASHTAM: A vulkni hamu elterjedsvel kapcsolatos rtestsek, amelyek akkor jelentek meg, amikor a vulknkitrsek vagy ms geolgiai esemnyek hatssal lehetnek a repls biztonsgra.&lt;br&gt;&lt;br&gt;Ezenkvl vannak egyb specilis NOTAM-ok, amelyek egy adott orszg vagy rgi lgikzlekedsi hatsgai ltal adhatk ki a replsi biztonsg s hatkonysg rdekben.",
